  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #2fa8cc;
  font-family: "Roboto", sans-serif;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.container {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: stretch;
  background-color: #f4f4f4;
  border-radius: 4px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 15px 5px;
  text-align: left;
  width: 92%;
}

div, li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 2.5rem;
  border-radius: 4px;
  border: 0;
  padding: 0.1rem;
  align-self: stretch;
  gap: 0.2rem;
}

img {
  height: 2rem;
}

div h1 {
  color: black;
  font-size: 1.2rem;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  letter-spacing: 0.0025rem;
}
div #userInput {
  width: 100%;
}

input {
  height: 2rem;
  border: 0;
  padding: 0.3rem;
  align-self: stretch;
  border-radius: 2px;
}

#addItem {
  flex: 0.9;
}

#checkB {
  flex: 0.1;
  border-radius: 4px;
  width: 5vw;
  align-self: center;
  cursor: pointer;
  transition: all 0.5s;
}

#recyclImg {
  flex: 0.1;
  opacity: 0.5;
}
#recyclImg:hover {
  opacity: 1;
}

#dotsImg {
  opacity: 7;
}
#dotsImg::active {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100%;
  background-repeat: no-repeat;
}

#clearAllBtn {
  transition: all 0.5s;
  border-radius: 2px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 0.001em;
  word-spacing: normal;
  background-color: #2fa8cc;
  color: #f4f4f4;
  border: 0;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 6px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  cursor: pointer;
  text-align: center;
}
#clearAllBtn:active {
  transform: scale(0.98);
}
#clearAllBtn:focus {
  outline: 0;
